.App {
  padding-left: 150px;
  padding-right: 150px;
  margin: 50px 10px;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.navbar {
  margin: 0px 20px;
  position: fixed;
  width: 100%;
  background-color: white;
  top: 0px;
  left: 0px;
}